export default {
	data() {
		return {
			admin: {},
			member: {},
			com_name: ''
		}
	},
	methods: {
		// 获取用户信息
		getUser() {
			let baseUrl = this.WebUrl
			let that = this
			uni.getStorage({
				key: 'u_id',
				success: (id) => {
					uni.request({
						url: baseUrl + '/getUser',
						data: {
							u_id: id.data
						},
						success: (res) => {
							that.getTeam(res.data.data.firm_id)
							that.com_name = res.data.data.com_name
						}
					})
				}
			})
		},
		
		// 获取团队
		getTeam(firm_id) {
			let baseUrl = this.WebUrl
			let that = this
			uni.request({
				url: baseUrl + '/getTeam',
				data: {
					firm_id: firm_id
				},
				success: (res) => {
					that.admin = res.data.data.admin
					that.member = res.data.data.member
					console.log(that.admin)
				}
			})
		},
	},
	onLoad() {
		this.getUser()
	}
}
